import React from "react"
import { css } from "@emotion/core"
import { Link, graphql } from "gatsby"
import { rhythm } from "../utils/typography"
import Layout from "../components/layout"
import mdlistStyle from "../styles/md-index-css-module.module.css"

export default ({ data }) => {
  return (
    <Layout>
      <div>
        <h1 className={mdlistStyle.header}>Markdown File Lists</h1>
        <h4 className={mdlistStyle.posts}>
          {data.allMarkdownRemark.totalCount} Posts
        </h4>
        {data.allMarkdownRemark.edges.map(({ node }) => (
          <div key={node.id}>
            <div>
              {/* ! 요 아래 Link가 제목을 나타내는 링크 */}
              <Link to={node.fields.slug} className={mdlistStyle.titles}>{node.frontmatter.title}</Link>
              <span className={mdlistStyle.date}> — {node.frontmatter.date}</span>
            </div>
            <p>{node.excerpt}</p>
          </div>
        ))}
      </div>
    </Layout>
  )
}

export const query = graphql`
  query {
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
          }
          fields {
            slug
          }
          excerpt
        }
      }
    }
  }
`
